header {
  align-items: center;
  display: flex;
  height: 30vh;
  justify-content: center;
  margin: auto;
  position: relative;
  width: 90vw;
  z-index: 3;
}

#starWars {
  height: 20vh;
}

#falcon {
  bottom: 0;
  left: 0;
  position: absolute;
  transform: translate(-16%, 50%);
  width: 25vw;
  z-index: 3;
}

#xWing {
  bottom: 0;
  position: absolute;
  right: 0;
  transform: translate(16%, 40%);
  width: 25vw;
  z-index: 3;
}

#deathStar {
  position: absolute;
  right: 0;
  top: 1vh;
  width: 10vw;
}

